.ProjectWrapper {
    padding: 20px;
    border-radius: 8px;
    margin-bottom: 20px;
  }
  
  .SectionTitle {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 15px;
  }
  
  .ToolsList {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    grid-gap: 20px;
  }
  
  .ToolCard {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 15px;
    width: 190px;
    height: 200px;
    border: 1px solid #ccc;
    border-radius: 8px;
    background-color: #fff;
    transition: transform 0.2s, box-shadow 0.2s;
  }
  
  .ToolCard:hover {
    transform: translateY(-5px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .ToolCard img {
    max-width: 80px;
    max-height: 80px;
    margin-bottom: 10px;
  }
  
  .ToolName {
    font-size: 16px;
    word-wrap: break-word;
  }